        max-width: 1200px;
        margin: 0 auto;
        padding: 10px;
    }
    .headlabel_dp6{
        height: 30px;
        background-color: #989898;
        color: white;
        font-size: 18px;
        font-weight: bold;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding-left: 5px;
    }
    .design_img_dp6{
        width: 55%;
        margin: 5px;
    }
    .pbia_dp6{
        page-break-inside: avoid !important;
    }
    .fs_14_dp6{
